html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

.page {
  height: 100%;
  font-family: $font-family, sans-serif;
  -webkit-text-size-adjust: 100%;
}

.page__body {
  color: $white-color;
  font-size: 16px;
  height: 100%;
  background-color: $bg-color;
  position: relative;
  &::after {
    content: "";
    position: fixed;
    background: url("../img/bg.png");
    background-position: right bottom;
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
  }
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.main {
}

